import React, { Suspense } from "react";
import Group from "./Group";
import "./Groups.css";

const ToKnockoutRounds = React.lazy(() => import("./ToKnockoutRounds"));

const Groups = (props) => {
  const moveHandler = (position) => {
    const element = document.querySelector(".groups-container");
    element.style.transform = `translateX(-${100 * position}%)`;
  };

  const teamsSelectedHandler = (selected) => {
    const newTeamsSelected = selected
      ? props.teamsSelected + 1
      : props.teamsSelected - 1;
    props.setTeamsSelected(newTeamsSelected);
  };

  const clickHandler = () => {
    const element = document.querySelector(".groups");
    element.style.transform = "translateY(-100%)";
    setTimeout(() => {
      props.setLoadBracket(props.teamsSelected === 16);
      window.scrollTo({
        behavior: "smooth",
        top: 0,
      });
    }, 500);
    props.setTeamsSelected(0);
    props.setSelections(15);
  };

  return (
    <div className="groups overflow-hidden h-screen">
      <div className="groups-container flex flex-nowrap flex-row groups-container-transition">
        {props.groupsData.map((group, i) => (
          <Group
            teams={group.teams}
            group={group.group}
            key={group.group}
            position={i}
            onMove={moveHandler}
            onResult={props.onResult}
            onTeamSelected={teamsSelectedHandler}
            totalTeamsSelected={props.teamsSelected}
          />
        ))}
      </div>
      {props.teamsSelected === 16 ? (
        <Suspense fallback={<div></div>}>
          <ToKnockoutRounds onClick={clickHandler} />
        </Suspense>
      ) : null}
    </div>
  );
};

Groups.defaultProps = {
  teamsSelected: 0,
};

export default Groups;
