import Checkbox from "@mui/material/Checkbox";
import SportsSoccerIcon from "@mui/icons-material/SportsSoccer";

import "./Team.css";

const Team = (props) => {
  const clickHandler = () => props.onSelected(props.team);
  const isPlaceHolderTeam = props.team.name === "Placeholder";

  const label =
    props.team.position === 1 && props.team.selected
      ? "1st"
      : props.team.position === 2 && props.team.selected
      ? "2nd"
      : "";
  const emoji =
    props.team.position === 1 && props.team.selected
      ? "ðŸ¥‡"
      : props.team.position === 2 && props.team.selected
      ? "ðŸ¥ˆ"
      : "";

  return (
    <div
      onClick={clickHandler}
      className={`team ${
        props.team.disabled
          ? "opacity-50 cursor-not-allowed hover:bg-opacity-100"
          : "hover:bg-opacity-50 cursor-pointer"
      } relative text-center grid grid-cols-4 gap-2 bg-slate-100 p-2 border-b-2 border-slate-300 last:border-none first:rounded-tl-3xl last:rounded-br-3xl ${
        props.size
      }`}
    >
      <span
        className="text-4xl md:text-5xl ml-1 md:ml-3 absolute -left-5 -top-2 z-10"
        role="img"
        aria-label={label}
      >
        {emoji}
      </span>
      <div className="relative flex items-center justify-center">
        <img
          className="team__image w-full py-1"
          src={`/images/${props.team.name.replaceAll(" ", "-")}.png`}
          alt={props.team.name}
        />
      </div>
      <div
        className={`col-span-2${
          props.size === "small" ? " text-xl" : " text-2xl md:text-3xl"
        } font-bold grid justify-items-start`}
      >
        <div className="flex items-center text-slate-500">
          {isPlaceHolderTeam ? "" : props.team.name}
        </div>
      </div>
      <Checkbox
        sx={{
          "& .MuiSvgIcon-root": { fontSize: props.size === "small" ? 32 : 48 },
        }}
        color="success"
        icon={<SportsSoccerIcon />}
        checkedIcon={<SportsSoccerIcon />}
        checked={props.team.selected}
        disabled={isPlaceHolderTeam}
      />
    </div>
  );
};

Team.defaultProps = {
  size: "default",
};

export default Team;
