import React, { useState } from "react";
import "./Group.css";
import Team from "../Team";
import Button from "@mui/material/Button";
import { styled } from "@mui/material/styles";
import { useSwipeable } from "react-swipeable";

const Group = (props) => {
  const [teams, setTeams] = useState(props.teams);
  const selectedHandler = (team) => {
    const selectedTeamsCount = teams.filter((t) => t.selected).length;
    let updatedTeams = [...teams];
    if (selectedTeamsCount < 2 || team.selected) {
      team.selected = !team.selected;
      props.onTeamSelected(team.selected);
      updatedTeams = updatedTeams.map((team1) =>
        team.id === team1.id ? team : team1
      );
    }
    let selectedTeams = updatedTeams.filter((t) => t.selected);
    let unselectedTeams = updatedTeams.filter((t) => !t.selected);
    if (selectedTeams.length === 2) {
      unselectedTeams = unselectedTeams.map((t) => {
        t.disabled = true;
        t.position = 3;
        return t;
      });
    } else {
      unselectedTeams = unselectedTeams.map((t) => {
        t.disabled = false;
        t.position = 3;
        return t;
      });
    }

    selectedTeams = selectedTeams.map((t, i) => {
      t.position = i + 1;
      return t;
    });
    setTeams(selectedTeams.concat(unselectedTeams));
    props.onResult(selectedTeams.map((t) => t.id).join(""), props.group);
  };

  const NextButton = styled(Button)({
    fontWeight: "bold",
    letterSpacing: "2px",
    padding: "8px 25px",
    fontSize: "20px",
    display: props.position < 7 ? "inline-flex" : "none",
    float: "right",
  });
  const PrevButton = styled(Button)({
    fontWeight: "bold",
    letterSpacing: "2px",
    padding: "8px 25px",
    fontSize: "20px",
    display: props.position > 0 ? "inline-flex" : "none",
    float: "left",
  });

  const isButtonDisabled = teams.filter((t) => t.selected).length < 2;

  const nextHandler = () => props.onMove(props.position + 1);
  const prevHandler = () => props.onMove(props.position - 1);

  const swipeHandlers = useSwipeable({
    onSwipedLeft: (e) => {
      if (e.absX > 100 && props.position < 7 && !isButtonDisabled) {
        props.onMove(props.position + 1);
      }
    },
    onSwipedRight: (e) => {
      if (e.absX > 100 && props.position > 0) {
        props.onMove(props.position - 1);
      }
    },
  });

  return (
    <div className="group w-full shrink-0" {...swipeHandlers}>
      <div className="group-container m-auto">
        <div className="text-xl md:text-2xl font-bold m-2 text-slate-600">
          Group {props.group}
        </div>
        <div className="relative rounded-tl-3xl rounded-br-3xl border-2 border-slate-400 mt-0 mb-2 mx-2 shadow-xl">
          {teams.map((team) => (
            <Team team={team} key={team.id} onSelected={selectedHandler} />
          ))}
        </div>
        <div className="relative p-2">
          <PrevButton
            className="font-bold move-button"
            variant="contained"
            color="success"
            onClick={prevHandler}
          >
            Back
          </PrevButton>
          <NextButton
            className="font-bold move-button"
            variant="contained"
            disabled={isButtonDisabled}
            color="success"
            onClick={nextHandler}
          >
            Next
          </NextButton>
        </div>
      </div>
    </div>
  );
};

export default Group;
