import Groups from "./Groups";
import { CookiesProvider, useCookies } from "react-cookie";
import { ProcessGroupStagePick } from "../../util/CookieResultManager";

const GroupStage = (props) => {
  const [cookie, setCookie] = useCookies(["result"]);
  function resultHandler(picks, group) {
    let result = cookie.result;
    result = ProcessGroupStagePick(result, picks, group);
    setCookie("result", result, { path: "/" });
  }

  return (
    <CookiesProvider>
      <Groups
        onResult={resultHandler}
        groupsData={props.groupsData}
        teamsSelected={props.teamsSelected}
        setTeamsSelected={props.setTeamsSelected}
        setSelections={props.setSelections}
        setLoadBracket={props.setLoadBracket}
      />
    </CookiesProvider>
  );
};

export default GroupStage;
